#add-modal {
    .modal-body {
		.breakLine{
			padding: 8px 4px;
			border-top: 1px dashed #ddd;
			border-bottom: 1px dashed #ddd;
			margin-bottom: 7px;
		}
		.radio-label>label{
			margin: 5px 15px;
		}
    }
}
.validate-msg{
	color: #a94442;
	display:none;
	height:18px;
	margin-top:5px;
	img{
		width: 16px;
		vertical-align: top;
		display: inline-block;
		margin: 0 10px 0 0px;
	}
	.validate-ctx{
		line-height:18px;
	}
}
.error-validate{
	border: 1px solid #a94442 !important;
	color: #a94442;
}



